import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import { Head } from 'react-static'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import classNames from 'classnames'
import { HEADER_HEIGHT, ACCENT_COLOR } from '../../constants/GlobalStyle'
import Footer from '../shared/Footer'
import HeroDiv from '../svg/HeroDiv'
import GitHub from '../svg/GitHub'
import ZhiHu from '../svg/ZhiHu'
import Twitter from '../svg/Twitter'

const styles = theme => ({
  root: {
    marginTop: HEADER_HEIGHT,
    backgroundColor: '#fff',
    fontFamily: 'sans-serif'
  },
  header: {
    position: 'relative',
    backgroundColor: ACCENT_COLOR,
    backgroundImage: `linear-gradient(60deg, #4bda99, #3da274)`,
    padding: `${theme.spacing.unit * 4}px 0`,
    [theme.breakpoints.up('sm')]: {
      padding: `${theme.spacing.unit * 8}px 0`
    }
  },
  container: {
    width: `calc(100% - 32px)`,
    padding: `${theme.spacing.unit * 6}px 0`,
    margin: `0 auto`,
    [theme.breakpoints.up('md')]: {
      width: 800
    },
    [theme.breakpoints.up('lg')]: {
      width: 1000
    },
    [theme.breakpoints.up('xl')]: {
      width: 1100
    }
  },
  hero: {
    position: 'absolute',
    bottom: 0,
    left: 0,
    height: 'auto',
    marginBottom: -1,
    width: '100%'
  },
  profile: {
    justifyContent: 'space-between'
  },
  avatar: {
    display: 'block',
    width: '100%',
    padding: 8
  },
  title: {
    fontSize: 22,
    color: '#212121',
    margin: '0 0 32px',
    textAlign: 'center',
    '&:after': {
      margin: '0 auto',
      content: '""',
      display: 'block',
      width: 36,
      height: 16,
      borderBottom: `3px solid ${ACCENT_COLOR}`
    }
  },
  authorWrapper: {
    display: 'flex'
  },
  author: {
    fontSize: 22,
    margin: '0 0 32px',
    color: '#fff',
    '&:after': {
      content: '""',
      display: 'block',
      width: 36,
      height: 16,
      borderBottom: `3px solid #fff`
    }
  },
  icon: {
    fill: '#fff',
    marginLeft: 16
  },
  link: {
    textDecoration: 'none'
  },
  intro: {
    lineHeight: 2.6,
    fontSize: 14,
    color: '#fff'
  },
  text: {
    lineHeight: 2.2,
    fontSize: 14,
    opacity: 0.7
  },
  wrapper: {
    height: '100%',
    padding: '24px 0',
    position: 'relative',
    '&:before': {
      content: '" "',
      position: 'absolute',
      width: 2,
      top: 0,
      bottom: 0,
      left: '50%',
      marginLeft: -1,
      background: '#ececec'
    }
  },
  item: {
    display: 'flex',
    alignItems: 'center',
    margin: '0 auto 32px',
    fontSize: 14,
    opacity: 0.7
  },
  date: {
    color: ACCENT_COLOR
  },
  left: {
    padding: 16,
    width: '50%',
    textAlign: 'right',
    lineHeight: 2
  },
  right: {
    padding: 16,
    width: '50%',
    lineHeight: 2,
    position: 'relative',
    '&:before': {
      content: '""',
      position: 'absolute',
      width: 10,
      height: 10,
      border: '2px solid #fff',
      borderRadius: '50%',
      left: -5,
      top: '50%',
      marginTop: -4,
      background: ACCENT_COLOR
    }
  }
})

const About = props => {
  const { classes: s } = props
  return (
    <div className={s.root}>
      <Head title="About" />
      <div className={s.header}>
        <div className={s.container}>
          <Grid container spacing={32} className={s.profile}>
            <Grid item xs={12} sm={6}>
              <div className={s.authorWrapper}>
                <div className={s.author}></div>
                <a
                  href="https://github.com/happypeter"
                  className={s.link}
                  target="_blank"
                >
                  <GitHub className={s.icon} />
                </a>
                <a
                  href="https://zhihu.com/people/peterlovemoney"
                  className={s.link}
                  target="_blank"
                >
                  <ZhiHu className={s.icon} />
                </a>
                <a
                  href="https://twitter.com/happypeter1983"
                  className={s.link}
                  target="_blank"
                >
                  <Twitter className={s.icon} />
                </a>
              </div>
              <div className={s.intro}>
                 happypeter2006 Linux Kernel
                Git
                 GIthub 3.6K follower
                 Asianux
                 C/C++/Python/PHP/Ruby/JS
                 RubyOnRails  React
                 CTO
                2014
              </div>
            </Grid>
            <Grid item xs={12} sm={4}>
              <Paper elevation={2}>
                <img
                  className={s.avatar}
                  src="https://img.haoqicat.com/2018103102.jpg"
                />
              </Paper>
            </Grid>
          </Grid>
        </div>

        <HeroDiv className={s.hero} />
      </div>

      <div className={s.container}>
        <div className={s.title}></div>
        <div className={s.text}>
           Nervos  Nervos 
          DApp React  JS Unix
           Git 
        </div>
      </div>

      <div className={s.container}>
        <div className={s.title}></div>
        <div className={s.wrapper}>
          <div className={s.item}>
            <div className={classNames(s.left, s.date)}>2018-09-21</div>
            <div className={s.right}></div>
          </div>
          <div className={s.item}>
            <div className={s.left}>
               Nervos AppChain  DApp 
            </div>
            <div className={classNames(s.right, s.date)}>2018-9-24</div>
          </div>
          <div className={s.item}>
            <div className={classNames(s.left, s.date)}>2018-10-24</div>
            <div className={s.right}></div>
          </div>

          <div className={s.item}>
            <div className={classNames(s.left, s.date)} />
            <div className={s.right}>...</div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  )
}

export default withStyles(styles)(About)



